.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.card--value {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  margin: 10px;
  border-radius: 20px;
  gap: 15px;
}


.values--container {
  display: flex;
}


.card--type {
  font-size: 10px;
}
